.page__section--get {
  overflow: hidden;
  margin-bottom: 6.597vw;
  padding-top: 0.903vw;
  padding-bottom: 0.903vw;
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  flex-direction: column;

  @include viewport-tablet {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-bottom: 60px;
    min-height: 100vh;
  }
}

.page-get__wrapper {
  flex: 1;
}

.page-get {
  position: relative;
  box-sizing: border-box;
  height: 100%;
  padding-top: 7.741vh;

  @include viewport-desktop {
    padding-top: 5vh;
  }

  @include viewport-tablet {
    padding-top: 39px;
    overflow: visible;
  }
}

.page-get__bg {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--color-blue);
  z-index: -1;
  border-radius: 6px;
  transition: all .2s ease-in;
  transform: scale(1.03);

  @include viewport-tablet {
    // transform: scale(1);
  }

  // &::after {
  //   content: "";
  //   position: absolute;
  //   width: 4.514vw;
  //   height: 6.667vw;
  //   left: -1.528vw;
  //   top: -3.958vw;
  //   background: #ffffff;
  //   transform: rotate(45deg);

  //   @include viewport-tablet {
  //     padding-top: 39px;
  //     width: 65px;
  //     height: 96px;
  //     top: -70px;
  //     left: -38px;
  //   }
  // }
}

.page-get__bg--active {
  transform: scale(1);
}

.page-get__title {
  max-width: 87.510vh;
  font-size: 6.695vh;
  line-height: 1;
  font-weight: 500;
  text-align: center;
  margin-bottom: 2.615vh;
  margin-left: auto;
  margin-right: auto;
  display: flex;

  @include viewport-desktop {
    font-size: 5vh;
    max-width: 75.51vh;
  }

  @include viewport-tablet {
    max-width: 69vh;
    font-size: 4.523vh;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    padding-right: 10px;
    padding-left: 10px;
  }

  @include viewport-phone {
  }
}

.page-get__content {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  max-width: 100%;
  top: -4vh;

  // @include viewport-tablet {
  //   display: none;
  // }
}

.page-get-slider__wrap {
  position: absolute;
	overflow: hidden;
  height: 56vh;
  width: 25.59vh;
  top: 6.4vh;
  left: 50%;
  transform: translateX(-50%);
}

.page-get-slider {
  position: absolute;
  z-index: 3;
  width: 100%;
  transition: 0.4s;

  &.item-2 {
    transform: translateY(-33.33%);
  }

  &.item-3 {
    transform: translateY(-66.66%);
  }
}

.page-get-slider__item {
  width: 100%;
}


.page-get__iphone {
  height: 68.519vh;
  position: relative;
  z-index: 5;
}

.page-get__img {
  position: absolute;
  left: calc(50%);
  top: 2.3vh;
  height: 59.519vh;
  
  width: 27.510vh;
  transform: translateX(-50%);
  transition: all .2s ease-in;
  opacity: 1;

  &.active {
    opacity: 0;
    transform: translateX(-50%) scale(1.5);
  }

  @include viewport-tablet {
    height: 57.519vh;
  }
}

