.page-get-success {
  height: 6.076vh;
  max-width: calc(100% - 0.694vw);
  left: 0.347vw;
  right: 0.347vw;
  width: 100%;
  position: absolute;
  top: 5%;
  // left: 50%;
  transform: translateY(-100%);
  z-index: 3;
  opacity: 0;
  padding: 1.083vh 1.405vh;
  box-sizing: border-box;
  background: rgba(37, 37, 37, 0.5);
  backdrop-filter: blur(32px);
  -webkit-backdrop-filter: blur(32px);
  border-radius: 1.111vw;
  display: flex;
  align-items: center;
  transition: all .2s ease-in;

  @media (max-width: 660px) {
    border-radius: 1.511vw;
  }

  @include viewport-phone {
    border-radius: 1.911vw;
  }

  // @include viewport-desktop {
  //   top: 6%;
  //   transform: translate(-50%, 0) scale(0.5);
  // }

  &.active {
    opacity: 1;
    transform: translateY(0);

    // @include viewport-desktop {
    //   transform: translate(-50%, 0) scale(0.87);
    // }
  }
}

.page-get-success__ico-wrap {
  width: 3.226vh;
  height: 3.226vh;
  margin-right: 0.860vh;
  position: relative;
}

.page-get-success__ico {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  object-fit: contain;
}

.page-get-success__content {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.page-get-success__box {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.page-get-success__title {
  margin-right: 1vw;
  font-size: 1.02vh;
  color: #ffffff;
  font-weight: 600;
  line-height: 1.2;

  @media (min-height: 962px) {
    font-size: .9vh;
  }
}

.page-get-success__date {
  font-size: 1.02vh;
  line-height: 1.2;
  color: #ffffff;
  opacity: .8;

  @media (min-height: 962px) {
    font-size: .9vh;
  }

}

.page-get-success__text {
  font-size: 1.02vh;
  color: #ffffff;
  line-height: 1.2;

  @media (min-height: 962px) {
    font-size: .9vh;
  }
}

.page-get__nav {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(100%, -50%);
  max-width: 21vh;

  @include viewport-desktop {
    max-width: 14.5vh;
    transform: translate(120%, -50%);
  }

  @include viewport-tablet {
    min-width: 30.51vh;
    max-width: 30.51vh;
    display: flex;
    position: relative;
    transform: none;
    left: auto;
    top: -3.2vh;
  }
}

.page-get__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 6.381vh;
  line-height: 1.1;
  font-weight: 500;
  font-size: 2.092vh;
  // cursor: pointer;
  transition: all .3s ease-in-out;
  opacity: .4;

  @include viewport-desktop {
    font-size: 1.5vh;
  }

  // &:hover {
  //   opacity: 1;
  // }

  @include viewport-tablet {
    display: none;
    font-size: 2.513vh;

  }

  &.active {
    opacity: 1;
    // cursor: default;

    @include viewport-tablet {
      display: flex;
      margin: 0;
      margin-left: 10px;
      margin-right: 10px;
    }
  }
}


.page-get__ico-wrap {

}

.page-get__ico {
  width: 1.674vh;
  height: 1.674vh;
  margin-bottom: 1.674vh;

  @include viewport-tablet {
    margin-bottom: .8vh;
  }
}

.page-get-mobile {
  display: none;

  @include viewport-tablet {
    display: block;
    padding-bottom: 150.667vw;
    position: relative;
  }
}

.page-get-mobile__list-wrap {
  position: absolute;
  top: 0;
  left: -10px;
  right: -10px;
  width: calc(100% + 20px);
  max-width: calc(100% + 20px);
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.page-get-mobile__list {
  display: block;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  text-align: center;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.page-get-mobile__item {
  display: inline-block;
  vertical-align: top;
  margin: 0 -4vw;
  position: relative;
  

  &:first-child {
    padding-left: 12vw;
  }

  &:last-child {
    :after {
      content: "";
      display: block;
      position: absolute;
      right: -24vw;
      width: 24vw;
      height: 1px;
    }
  }
}

.page-get-mobile__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.page-get-mobile__img-wrap {
  width: 66.667vw;
}

.page-get-mobile__img {
  width: 100%;
}

.page-get-mobile__text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  line-height: 1.1;
  font-weight: 500;
  font-size: 5.333vw;
  cursor: pointer;
  margin-top: -4vw;
  margin-left: 25vw;
  text-align: left;
  max-width: 53vw;

  span {
    white-space: normal;
  }
}

.page-get-mobile__ico-wrap {}

.page-get-mobile__ico {
  width: 4.267vw;
  height: 4.267vw;
  margin-bottom: 4.267vw;
}